.popup__container {
  max-width: 430px;
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  color: #000;
  border-radius: 10px;
  position: relative;
  padding: 34px 36px 37px;
}

@media screen and (max-width: 810px) {
  .popup__container {
    padding: 25px 22px;
    max-width: 282px;
  }
}
